import React from 'react'

const Edit = (props) => {
    return (
        <>
            <tr className="back rounded-pill">
                <td><input type="text" value={props.value} onChange={props.handle1} className="form-control"/></td>

                <td colspan="2" className="text-end">
                <div className="d-grid gap-2">
                <input type="submit" onClick={()=>{props.update(props.id)}} value="Update" className="btn btn-outline-success rounded-pill px-4"/>
                    </div>

                    
                </td>
            </tr>
            
        </>
    )
}

export default Edit
