import React from 'react'

const Plan = (props) => {
    return (
        <>
                <tr>
                    <th>{props.id + 1}</th>
                    <td>{props.value}</td>
                    <td className="text-end"><input type="submit" className="btn btn-danger fw-bold px-4" value="Delete" onClick={()=>{props.delete(props.id)}}/></td>

                    <td className="text-end"><input type="submit" className="btn btn-warning fw-bold px-4" value="Edit" onClick={()=>{props.edit(props.id)}}/></td>
                </tr>
        </>
    )
}

export default Plan
