import React, { Component } from 'react'
import '../node_modules/bootstrap/dist/css/bootstrap.min.css'
import Plan from './Plan'
import Edit from './Edit'
import Empty from './Empty'


export default class Today extends Component {
    state = {
        lists:[],
        text:"",
        edit1:"all",
        text2:"",
    }

    handle = e => {
        if(e.target.name==="Base")
            this.setState({text:e.target.value})
        else
            this.setState({text2:e.target.value})
    }
    handleAdd = e =>{
        if(this.state.text!==""){
            const item = [...this.state.lists,this.state.text];
            this.setState({lists:item,text:""});
        }
    }

    handleUpdate = id =>{
      
        if(this.state.text2!==""){
            const oldItems = [...this.state.lists];
            const lists = oldItems.map((value,i) =>{
                return i!==id?value:this.state.text2;
            })
            this.setState({lists:lists,text2:"",edit1:"all"});
        }       
    }

    handleDelete = id =>{
            const oldItems = [...this.state.lists];
            const lists = oldItems.filter((element,i)=>{
                return i !==id;
            })
            this.setState({lists:lists});
    }

    handleEdit = (id) =>{
        this.setState({lists:this.state.lists,text:this.state.text,edit1:id});
        this.state.lists.forEach((value,index)=>{
            if(index===id){
                this.setState({text2:value})
            }
        })
    }

    render() {
        
         const el =  this.state.lists.map((value,i) =>{
                return (Number.parseInt(this.state.edit1)===(i)) ? <Edit value={this.state.text2} key={i} id={i} handle1={this.handle} update={this.handleUpdate}/> : <Plan value={value} key={i} id={i} delete={this.handleDelete} edit={this.handleEdit}/> 
            })
        
        return (
            <div className="container-fluid">
                <div className="row ">
                    <div className=" col-md-9 mx-auto shadow-lg p-3 my-5 rounded-3 ">
                        <h1 className="text-center py-3 myshadow">My Today's Plan List</h1>
                        <div className="row p-3">
                            <div className="col-md-8">
                                <input type="text" className="form-control" placeholder="Entre Your Plan's" name="Base" value={this.state.text} onChange={this.handle}/>
                            </div>
                            <div className="col-md-3 text-center sm-m-2 pt-2">
                                <input type="submit" className=" text-center btn btn-outline-success fw-bold px-5 pt-2" value="Add" onClick={this.handleAdd}/>
                            </div>
                        </div>
                        <div className="container-fluid">
                            <div className="row">
                                
                                    
                            <table className="table">
                                <tbody>
                            
                                {
                                     this.state.lists.length===0 ?<Empty />:el
                                }
                            
                            </tbody>
                            </table>
                       
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}
